.info-block {
  width: 60%;
  padding: 20px;
  margin: auto;

  img {
    width: 100%;
    border-radius: 15px;
    object-fit: cover;
    margin-bottom: 15px;
    box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  }

  p {
    font-size: 21px;
    font-weight: bold;
    text-align: center;
    color: rgb(228, 15, 15);
  }
}