.header {
    background-color: rgba(0,0,0, .5);
    height: 60px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 50px;

    &__link {
        display: flex;
        margin-right: 40px;
        font-size: 18px;
        color: #fff;
        text-decoration: none;

        &:hover {
            color: #6ceb87;
        }
    
        &:active {
            transform: scale(0.9);
        }
    }
    
    &__cart {
        width: 20px;
        margin-right: 10px;
    }
}